import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import { enquireScreen } from 'enquire-js';
import Header from './components/Layout/Header';
import Footer from './components/Layout/Footer';
import {
  Home,
  About,
  ComingSoon,
  LegalDisclaimer,
  PrivacyPolicy,
  TermsOfUse,
} from './pages';
import classNames from 'classnames/bind';
import styles from './App.scss';

const cx = classNames.bind(Object.assign({}, styles));

let isMobile: boolean;
enquireScreen((isM: boolean) => {
  isMobile = isM;
});

class App extends Component {
  state = {
    isMobile,
  };

  componentDidMount() {
    enquireScreen((isM: boolean) => {
      this.setState({
        isMobile: !!isM,
      });
    });
  }

  render() {
    return (
      <div>
        <Header isMobile={this.state.isMobile} />
        <div className={cx('Content')}>
          <Route exact path="/" component={Home} />
          <Route exact path="/about" component={About} />
          <Route exact path="/coming-soon" component={ComingSoon} />
          <Route exact path="/privacy-policy" component={PrivacyPolicy} />
          <Route exact path="/terms-of-use">
            <TermsOfUse isMobile={this.state.isMobile} />
          </Route>
          <Route exact path="/legal-disclaimer">
            <LegalDisclaimer isMobile={this.state.isMobile} />
          </Route>
          <Route path="*">
            <div> 이건 아닌데... </div>
          </Route>

          {/* <Route exact path="/web-test/" component={Home} />
          <Route exact path="/web-test/about" component={About} />
          <Route exact path="/web-test/coming-soon" component={ComingSoon} />
          <Route
            exact
            path="/web-test/privacy-policy"
            component={PrivacyPolicy}
          />
          <Route exact path="/web-test/terms-of-use">
            <TermsOfUse isMobile={this.state.isMobile} />
          </Route>
          <Route exact path="/web-test/legal-disclaimer">
            <LegalDisclaimer isMobile={this.state.isMobile} />
          </Route> */}
        </div>
        <Footer isMobile={this.state.isMobile} />
      </div>
    );
  }
}

export default App;
