import React, { Component } from 'react';
import { Helmet } from "react-helmet";
import { Row, Col } from 'antd';
import classNames from 'classnames/bind';
import styles from './ComingSoon.scss';

const cx = classNames.bind(Object.assign({}, styles));

class ComingSoon extends Component {
  render() {
    return (
      <Row className={cx('ComingSoon')}>
        <Helmet>
          <title>K Innovation Center :: About</title>
        </Helmet>
        <Col span={24}>
          <Row>
            <Col span={24} className={cx('Section__Mission', 'ComingSoon__Main')}>
              <h1 className={cx('Title', 'Text-bright')}>2022 Programs</h1>
              <h1 className={cx('Title', 'Text-bright')}>Why K Innovation Center?</h1>
              <h1 className={cx('Title', 'Text-bright')}>Our Stars</h1>
            </Col>
          </Row>
        </Col>
      </Row>
    )
  }
};

export default ComingSoon;
