import React, { Component } from 'react';
import { Row, Col, Carousel } from 'antd';
import classNames from 'classnames/bind';
import styles from './Home.scss';

const cx = classNames.bind(Object.assign({}, styles));

class Home extends Component {
  render() {
    return (
      <Row className={cx('Home')}>
        <Col span={24}>
          <Row>
            <Col span={24} className={cx('Section__Mission')}>
              <h1 className={cx('Title', 'Text-bright')}>Our Mission</h1>
              <p className={cx('Text-center', 'Text-strong')}>Korean and Korean American startups are taking an unprecedented step toward the new market.</p>
              <p className={cx('Text-center', 'Text-strong')}>We are here to support you from start to finish.</p>
            </Col>
          </Row>
          <Row className={cx('Section', 'grey-back')}>
            <Col span={24}>
              <Row justify='center'>
                <Col md={16} xs={22} className={cx('Text-center')}>
                  <h1 className={cx('Title')}>Our Focus</h1>
                  <p>We focus on your measurable success</p>
                  <p>We focus on your market adaptation and validation</p>
                  <p>We focus on your long term viability and sustainability</p>
                </Col>
              </Row>
            </Col>
          </Row>
          <Row className={cx('Section')}>
            <Col span={24}>
              <Row justify='center'>
                <Col md={16} xs={22}>
                  <h1 className={cx('Text-center', 'Title')}>Our Story</h1>
                  <div className={cx('Title__line')} />
                  <p className={cx('Text-center')}>
                    K Innovation Center was established in 2014, with single purpose to help Korean startups succeed in US market.
                  </p>
                  <p className={cx('Text-center')}>
                    Since the inception, the Korean start up scene has gone through a dramatic change, with more than 25 unicorns* and $4B* invested in the first half of 2021.
                  </p>
                  <p className={cx('Text-center')}>
                    Korean startups are ready to launch their product and services in overseas. 
                  </p>
                  <p className={cx('Text-center')}>
                    The biggest messenger app in Japan is from Korea. The biggest entertainment app in Taiwan is from Korea. The biggest app in Middle East is from Korea.
                  </p>
                  <p className={cx('Text-center')}>
                    K Innovation Center is here to help bridge the startups with US and Canada market.
                  </p>
                  <p className={cx('Text-center')}>
                    * A quick recap of 1H 2021 hightlights
                  </p>
                  <p className={cx('Text-center')}>
                    <a href="https://infogram.com/2021-1hzj4o3j505mo4p?live" target="_blank" rel="noreferrer">https://infogram.com/2021-1hzj4o3j505mo4p?live</a>
                  </p>
                </Col>
              </Row>
            </Col>
          </Row>
          <Row className={cx('Section')}>
            <Col span={24}>
              <Row justify='center'>
                <Col md={16} xs={22} className={cx('Text-center')}>
                  <h1 className={cx('Title')}>Contact Us</h1>
                  <div className={cx('Title__line')} />
                  <p>info@kicsv.org</p>
                  <p>Co Working Space Location</p>
                  <p>3003 North 1st Street, San Jose, CA 95134</p>
                </Col>
              </Row>
              <Row justify='center'>
                <Col md={18} xs={24}>
                  <Carousel autoplay>
                    <div>
                      <div className={cx('Carousel__content', 'office1')} />
                    </div>
                    <div>
                      <div className={cx('Carousel__content', 'office2')} />
                    </div>
                    <div>
                      <div className={cx('Carousel__content', 'office3')} />
                    </div>
                  </Carousel>
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
      </Row>
    )
  }
};

export default Home;
