import React, { Component } from 'react';
import { Row, Col } from 'antd';
import classNames from 'classnames/bind';
import globalStyles from '../../App.scss';
import styles from './Layout.scss';
import logo from '../../assets/images/logo.png';
import oldLogo from '../../assets/images/kicsv.png';
import { AiOutlineFacebook, AiOutlineInstagram, AiOutlineLinkedin } from 'react-icons/ai';

const cx = classNames.bind(Object.assign({}, globalStyles, styles));

interface FooterProps {
  isMobile: boolean;
};

class Footer extends Component<FooterProps> {
  render() {
    return (
      <Row className={cx('Footer')} justify='center'>
        <Col md={18} xs={22}>
          <Row className={cx('Footer__content')} justify={this.props.isMobile ? 'center' : 'center'} align="middle">
            <Col md={{ span: 4, order: 1 }} xs={{ span: 22, order: 2 }}>
              <Row className={cx('Row__logos')} justify={this.props.isMobile ? 'space-between' : 'start'}>
                <Col md={16} sm={6} xs={10}>
                  <img className={cx('Footer__logo')} src={logo} alt="K Innovation Center" />
                </Col>
                {this.props.isMobile && (
                  <Col md={{ span: 5 }} sm={4} xs={7}>
                    <img className={cx('Footer__logo')} src={oldLogo} alt="KIC" />
                  </Col>
                )}
              </Row>
              <Row className={cx('mt10')} justify={this.props.isMobile ? 'center' : 'start'}>
                <Col>
                  <p className={cx('Text-small')}>&copy; 2022 KIC Silicon Valley</p>
                </Col>
              </Row>
            </Col>
            <Col md={{ span: 16, order: 2 }} xs={{ span: 22, order: 1 }} className={cx('Footer__links')}>
              <Row justify={'center'} gutter={24}>
                <Col>
                  <a href="https://www.facebook.com/kicsv" target="_blank" rel="noreferrer" className={cx('Social')}><AiOutlineFacebook /></a>
                </Col>
                <Col>
                  <a href="https://www.linkedin.com/in/kicsv/" target="_blank" rel="noreferrer" className={cx('Social')}><AiOutlineLinkedin /></a>
                </Col>
                <Col>
                  <a href="https://www.instagram.com/kicsv/" target="_blank" rel="noreferrer" className={cx('Social')}><AiOutlineInstagram /></a>
                </Col>
              </Row>
              <ul className={cx('links')}>
                <li className={cx('list-inline')}>
                  <a className={cx('Footer__link')} href="/legal-disclaimer">Legal Disclaimer</a>
                </li>
                <li className={cx('list-inline', 'divider')}>|</li>
                <li className={cx('list-inline')}>
                  <a className={cx('Footer__link')} href="/privacy-policy">Privacy Policy</a>
                </li>
                <li className={cx('list-inline', 'divider')}>|</li>
                <li className={cx('list-inline')}>
                  <a className={cx('Footer__link')} href="/terms-of-use">Terms of Use</a>
                </li>
              </ul>
            </Col>
            {!this.props.isMobile && (
              <Col md={{ span:4, order: 3}}>
                <img className={cx('Footer__logo', 'logo__kic')} src={oldLogo} alt="KIC" />
              </Col>
            )}
          </Row>
        </Col>
      </Row>
    );
  }
}

export default Footer;
