import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Row, Col } from 'antd';
import classNames from 'classnames/bind';
import globalStyles from '../../App.scss';
import styles from './Layout.scss';
import logo from '../../assets/images/logo.png';
import { HistoryInterface, LocationInterface } from '../../interfaces';

const cx = classNames.bind(Object.assign({}, globalStyles, styles));

interface HeaderProps {
  isMobile: boolean;
  location: LocationInterface;
  history: HistoryInterface;
};

class Header extends Component<HeaderProps> {
  state = {
    phoneMenuOpened: undefined,
    currentPage: '/',
  };

  componentDidMount() {
    this.setState({
      currentPage: this.props.location.pathname,
    });
  }

  componentDidUpdate(prevProps: HeaderProps) {
    if (this.props.location !== prevProps.location) {
      this.setState({
        currentPage: this.props.location.pathname,
      });
    }
  }

  onClickMobileMenu = () => {
    this.setState({
      phoneMenuOpened: !this.state.phoneMenuOpened,
    });
  }

  onClickMenu = (page: string) => {
    this.setState({
      phoneMenuOpened: false,
    });
    this.props.history.push(page);
  }

  render() {
    const { isMobile } = this.props;
    const { phoneMenuOpened, currentPage } = this.state;
    return (
      <Row>
        <Col span={24}>
          <Row justify='space-between' className={cx('Header')}>
            <Col>
              <img onClick={() => this.onClickMenu('')} className={cx('logo')} src={logo} alt="KIC" />
            </Col>
            <Col>
              {isMobile && (
                <div className={cx('Menu__mobile', { open: phoneMenuOpened })} onClick={this.onClickMobileMenu}>
                  <em className={cx('Menu__mobile-em')} />
                  <em className={cx('Menu__mobile-em')} />
                  <em className={cx('Menu__mobile-em')} />
                  <em className={cx('Menu__mobile-em')} />
                </div>
              )}
              {!isMobile && (
                <div className={cx('Menu', 'Menu-inline', { 'Menu-hidden': !phoneMenuOpened })}>
                  <div
                    className={cx({
                      'Menu-selected': currentPage.length === 1,
                    }, 'mr80', 'Menu__item', 'Text-click')}
                    onClick={() => this.onClickMenu('')}
                  >
                    Home
                  </div>
                  <div
                    className={cx({
                      'Menu-selected': currentPage.includes('about'),
                    }, 'mr80', 'Menu__item', 'Text-click')}
                    onClick={() => this.onClickMenu('about')}
                  >
                    About
                  </div>
                  <div
                    className={cx({
                      'Menu-selected': currentPage.includes('coming-soon'),
                    }, 'Menu__item', 'Text-click')}
                    onClick={() => this.onClickMenu('coming-soon')}
                  >
                    Coming Soon
                  </div>
                </div>
              )}
            </Col>
          </Row>
          {isMobile && (
            <div className={cx('Menu', 'Menu-horizontal', { 'Menu-show': phoneMenuOpened })}>
              <div
                className={cx({
                  'Menu-selected': currentPage.length === 1,
                }, 'mb10', 'Menu__item', 'Text-click')}
                onClick={() => this.onClickMenu('')}
              >
                Home
              </div>
              <div
                className={cx({
                  'Menu-selected': currentPage.includes('about'),
                }, 'mb10', 'Menu__item', 'Text-click')}
                onClick={() => this.onClickMenu('about')}
              >
                About
              </div>
              <div
                className={cx({
                  'Menu-selected': currentPage.includes('coming-soon'),
                }, 'Menu__item', 'Text-click')}
                onClick={() => this.onClickMenu('coming-soon')}
              >
                Coming Soon
              </div>
            </div>
          )}
        </Col>
      </Row>
    );
  }
}

export default withRouter(Header);
