import React, { Component } from 'react';
import { Helmet } from "react-helmet";
import { Row, Col } from 'antd';
import classNames from 'classnames/bind';
import styles from './About.scss';
import FiveHundredStartups from '../../assets/images/partners/500Startups.png';
import Alchemist from '../../assets/images/partners/Alchemist.png';
import WTIA from '../../assets/images/partners/WTIA.png';
import FuturePlay from '../../assets/images/partners/FuturePlay.png';
import Bonangels from '../../assets/images/partners/Bonangels.png';
import Mashup from '../../assets/images/partners/MashupAngels.png';
import Primer from '../../assets/images/partners/PrimerSazzePartners.png';
import Kakaovc from '../../assets/images/partners/KakaoVentures.png';
import FastVC from '../../assets/images/partners/FastVC.png';
import BassInvestment from '../../assets/images/partners/BASSInvestment.png';
import SpringCamp from '../../assets/images/partners/SpringCamp.png';
import BluepointPartners from '../../assets/images/partners/BluepointPartners.png';
import Cooley from '../../assets/images/partners/Cooley.png';
import WeWork from '../../assets/images/partners/wework.png';
import Moloko from '../../assets/images/partners/Moloko.png';
import Assistagram from '../../assets/images/partners/Assistagram.png';
import FenwickAndWest from '../../assets/images/partners/FenwickAndWest.png';
import BAKG from '../../assets/images/partners/BAKG.png';
import KOTRA from '../../assets/images/partners/KOTRA.png';
import KDB from '../../assets/images/partners/KDB.png';
import KVIC from '../../assets/images/partners/kvic.png';
import NRF from '../../assets/images/partners/nrf.png';
import 과기부 from '../../assets/images/partners/과학기술정보통신부.png';
import NIPA from '../../assets/images/partners/NIPA.png';
import 중기부 from '../../assets/images/partners/중기부.png';
import 산자부 from '../../assets/images/partners/산자부.png';
import SBA from '../../assets/images/partners/SBA.png';
import FourDReplay from '../../assets/images/startups/1_4D_replay.png';
import ZConverter from '../../assets/images/startups/2_Z_Converter.png';
import Stratio from '../../assets/images/startups/3_Stratio.png';
import ThreeI from '../../assets/images/startups/4_3I.png';
import 한국축산데이터 from '../../assets/images/startups/5_한국축산_데이터.png';
import TwelveCM from '../../assets/images/startups/6_12cm.png';

const cx = classNames.bind(Object.assign({}, styles));

const partnerGroups = [
  {
    partners: [
      {
        alt: '500 Startups',
        src: FiveHundredStartups,
      },
      {
        alt: 'Alchemist',
        src: Alchemist,
      },
      {
        alt: 'WTIA',
        src: WTIA,
      },
    ],
  },
  {
    partners: [
      {
        alt: 'Futureplay',
        src: FuturePlay,
      },
      {
        alt: 'Bonangels',
        src: Bonangels,
      },
      {
        alt: 'Mashup',
        src: Mashup,
      },
      {
        alt: 'Primer Sazze Partners',
        src: Primer,
      },
      {
        alt: 'Kakaovc',
        src: Kakaovc,
      },
      {
        alt: 'Fast VC',
        src: FastVC,
      },
      {
        alt: 'Bass Investment',
        src: BassInvestment,
      },
      {
        alt: 'Spring Camp',
        src: SpringCamp,
      },
      {
        alt: 'Bluepoint Partners',
        src: BluepointPartners,
      },
    ],
  },
  {
    partners: [
      {
        alt: 'Cooley',
        src: Cooley,
      },
      {
        alt: 'WeWork',
        src: WeWork,
      },
      {
        alt: 'Moloko',
        src: Moloko,
      },
      {
        alt: 'Assistagram',
        src: Assistagram,
      },
      {
        alt: 'Fenwick and West',
        src: FenwickAndWest,
      },
    ],
  },
  {
    partners: [
      {
        alt: 'NIPA',
        src: NIPA,
      },
      {
        alt: '과기정통부',
        src: 과기부,
      },
      {
        alt: 'Bay Area K Group',
        src: BAKG,
      },
      {
        alt: '한국연구재단',
        src: NRF,
      },
      {
        alt: 'KDB',
        src: KDB,
      },
      {
        alt: 'KVIC',
        src: KVIC,
      },
      {
        alt: '중기부',
        src: 중기부,
      },
      {
        alt: '산자부',
        src: 산자부,
      },
      {
        alt: 'SBA',
        src: SBA,
      },
      {
        alt: 'KOTRA',
        src: KOTRA,
      },
    ],
  },
];

const startups = [
  {
    name: '4DReplay',
    src: FourDReplay,
    url: 'https://4dreplay.com/?ckattempt=1',
  },
  {
    name: 'Z Converter',
    src: ZConverter,
    url: 'https://www.zconverter.com/',
  },
  {
    name: 'Stratio',
    src: Stratio,
    url: 'https://www.stratio.com/home',
  },
  {
    name: '3I',
    src: ThreeI,
    url: 'https://www.3i.ai/',
  },
  {
    name: '한국축산 데이터',
    src: 한국축산데이터,
    url: 'https://www.aidkr.com/en',
  },
  {
    name: '12cm',
    src: TwelveCM,
    url: 'https://www.12cm.co.kr/',
  },
];

class About extends Component {
  render() {
    return (
      <Row className={cx('About')}>
        <Helmet>
          <title>K Innovation Center :: About</title>
        </Helmet>
        <Col span={24}>
          <Row>
            <Col span={24} className={cx('Section__Mission', 'About__Main')}>
              <h1 className={cx('Title', 'Text-bright')}>Our Scope</h1>
              <p className={cx('Text-center', 'Text-strong')}>Source the best of the best Korean/Korean American startups ready to launch in N. America</p>
              <p className={cx('Text-center', 'Text-strong')}>Work closely with prominent Silicon Valley accelerators</p>
              <p className={cx('Text-center', 'Text-strong')}>Connect businesses and capital</p>
              <p className={cx('Text-center', 'Text-strong')}>Networking events for startups and venture capitalists</p>
              <p className={cx('Text-center', 'Text-strong')}>Support US and Canada startups entering Korea/Asia market</p>
            </Col>
          </Row>
          <Row className={cx('Section', 'grey-back')}>
            <Col span={24}>
              <Row justify='center'>
                <Col md={16} xs={22} className={cx('Text-center')}>
                  <h1 className={cx('Title')}>Our Team</h1>
                  <p>Programs Director</p>
                  <p>Accelerating Program Managers</p>
                  <p>PR Director</p>
                  <p>Marketing</p>
                  <p>Business Development and Investment Relations</p>
                </Col>
              </Row>
            </Col>
          </Row>
          <Row className={cx('Section')}>
            <Col span={24}>
              <Row justify='center'>
                <Col md={16} xs={22}>
                  <h1 className={cx('Text-center', 'Title')}>Our Partners</h1>
                  <div className={cx('Title__line')} />
                  {partnerGroups.map((group, index) => {
                    return (
                      <Row key={index} gutter={[24, 24]} className={cx({Partner__Group: index !== partnerGroups.length - 1})} justify='center'>
                        {group.partners.map((partner, id) => {
                          return (
                            <Col key={id} md={3} sm={6} xs={12}>
                              <img className={cx('Partner__logo')} src={partner.src} alt={partner.alt} title={partner.alt} />
                            </Col>
                          )
                        })}
                      </Row>
                    )
                  })}
                </Col>
              </Row>
            </Col>
          </Row>
          <Row className={cx('Section')}>
            <Col span={24}>
              <Row justify='center'>
                <Col md={16} xs={22} className={cx('Text-center')}>
                  <h1 className={cx('Title')}>Korean Startups Story</h1>
                  <div className={cx('Title__line')} />
                  <Row gutter={[24, 24]} justify='center'>
                    {startups.map((startup, index) => {
                      return (
                        <Col key={index} md={3} sm={6} xs={12}>
                          <a href={startup.url} target="_blank" rel="noreferrer"><img className={cx('Partner__logo')} src={startup.src} alt={startup.name} /></a>
                        </Col>
                      )
                    })}
                  </Row>
                </Col>
              </Row>
            </Col>
          </Row>
        </Col>
      </Row>
    )
  }
};

export default About;
